.gallery__item picture {
    max-height: 343px;
    height: 100%;
}

.gallery__item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 30px;
}

.gallery__item-discp {
    position: absolute;
    bottom: 12px;
    left: 12px;
    right: 12px;
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: var(--white);
    border-radius: 40px;
    padding: 16px 20px;
}

.gallery__items {
    display: flex;
    flex-wrap: wrap;
}

.gallery__item {
    flex: 0 0 calc(33.333% - 20px);
    margin: 10px;
    opacity: 1;
    max-height: 1000px;
    opacity: 1;
    transform: scale(1);
    transition: margin 0.3s, opacity 0.3s ease, transform 0.3s ease, max-height 0.3s ease;
    position: relative;
    border-radius: 30px;
    display: flex;
    flex-direction: column;
}

.gallery__item.hide {
    opacity: 0;
    pointer-events: none;
    transform: scale(0.65);
    max-height: 0;
    margin: 0;
}

@media (width <=1200px) {
    .gallery__item {
        flex: 0 0 calc(50% - 20px);
        border-radius: 16px;
    }
}

@media (width <=1200px) {
    .gallery__item-discp {
        font-size: 14px;
    }
}

@media (width <=867px) {
    .gallery__item-discp {
        font-size: 14px;
        position: relative;
        background: #ffffff00;
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 14px;
        line-height: 130%;
        color: var(--text-800);
        padding: 0;
    }

    .gallery__item picture {
        width: 100%;
        margin-bottom: 19px;
        max-height: 220px;
        height: 100%;
    }
}

@media (width <=480px) {
    .gallery__item {
        margin: 0;
        margin-bottom: 14px;
        flex: 0 0 100%;
    }
}

.hiding__element-buttons {
    display: flex;
    align-items: center;
    justify-content: center;
}

.hiding__element-buttons.hide {
    display: none;
}

.hiding__elements {
    margin-bottom: 40px;
}

.hiding__element-button {
    border: 1px solid var(--blue-line);
    border-radius: 30px;
    padding: 16px 30px;
    width: fit-content;
    font-weight: 500;
    font-size: 18px;
    color: var(--main-color);
    min-width: 194px;
    background-color: rgba(245, 222, 179, 0);
    transition: color 0.3s, border 0.3s, background-color 0.3s;
}

.hiding__element-button:hover {
    border: 1px solid var(--main-color);
    background-color: var(--main-color);
    color: white;
}

.hiding__element-button.open::after {
    content: "Скрыть";
}

.hiding__element-button::after {
    content: "Смотреть еще";
}

.hiding__element-button:active {
    background: var(--additional-color);
    opacity: 0.8;
}

@media (width <=1024px) {
    .hiding__elements {
        margin-bottom: 30px;
    }
}

@media (width <=480px) {
    .hiding__element-button {
        width: 100%;
    }
}